	<div id="app">
		<router-view name="header"></router-view>
		<router-view></router-view>
		<router-view name="footer"></router-view>
	</div>
</template>

<script>
	export default {
		methods: {
			
		}
	}
</script>

<style>
/*通用样式*/
html{-webkit-text-size-adjust: 100%; font-size: 62.5%;}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input,button, textarea, p, blockquote, th, td, hr{margin: 0; padding: 0;}
/*body{font-family:"Microsoft YaHei",\5FAE\8F6F\96C5\9ED1, Arial, sans-serif;font-size: 14px; line-height: 1.5;width:100%;}*/
body{font-family: Arial,"Microsoft YaHei";font-size: 14px; line-height: 1.4;width:100%;color: #333;}
article, aside, details, figcaption, figure, footer, header, main, nav, section, summary{display: block;}
audio, canvas, progress, video{display: inline-block; vertical-align: baseline;}
audio:not([controls]){display: none; height: 0;}
[hidden], template{display: none;}
svg:not(:root){overflow: hidden;}
a{background: transparent;text-decoration:none;-webkit-tap-highlight-color:transparent;}
a:active{outline:0;}
abbr[title]{border-bottom:1px dotted;}
b, strong{font-weight:bold;}
dfn{font-style:italic;}
mark{background:#ff0; color: #000;}
small{font-size:80%;}
sub, sup{font-size: 75%; line-height: 0; position: relative; vertical-align: baseline;}
sup{top:-0.5em;}
sub{bottom:-0.25em;}
img{border:0;}
hr{box-sizing: content-box; height: 0;}
pre{overflow: auto; word-wrap: break-word;}
code, kbd, pre, samp{font-family: monospace, monospace; font-size: 1em;}
button, input, optgroup, select, textarea{color: inherit; font: inherit; margin: 0;}
button{overflow: visible;}
button, select{text-transform: none;}
button, html input[type="button"], input[type="reset"], input[type="submit"]{-webkit-appearance: button; cursor: pointer;}
button[disabled], html input[disabled]{cursor: default;}
input{line-height: normal;}
input[type="checkbox"], input[type="radio"]{box-sizing: border-box; padding: 0;}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{color: #ccc;}
fieldset{border: 1px solid #c0c0c0; margin: 0 2px; padding: 0.35em 0.625em 0.75em;}
legend{border: 0; padding: 0;}
textarea{overflow: auto; resize: vertical;}
i ,b ,em{ font-style:normal;}
optgroup{font-weight: bold;}
table{border-collapse: collapse; border-spacing: 0;}
td, th{padding: 0;}
ul, ol{list-style: none outside none;}
h1, h2, h3 {line-height:1.5; font-weight: normal;}
h1{font-size: 18px;}
h2{font-size: 16px;}
h3{font-size: 14px;}
.clear{clear: both;*zoom: 1;}
.main{width: 1200px;margin-left: auto;margin-right: auto;}
.text_overflow{overflow: hidden;text-overflow: ellipsis;white-space: nowrap;display: block;}
.text_overflow2{display: -webkit-box;  -webkit-box-orient: vertical;  -webkit-line-clamp: 2;overflow: hidden;}
.text_overflow3{display: -webkit-box;  -webkit-box-orient: vertical;  -webkit-line-clamp: 3;overflow: hidden;}
.placeholder{width:100%;height:0;overflow:hidden;}
a{color: #333;}
.h10{height: 10px;clear: both;display: block;}
.h20{height: 20px;clear: both;display: block;}
.h50{height: 50px;clear: both;display: block;}
.h60{height: 60px;clear: both;display: block;}
.h70{height: 70px;clear: both;display: block;}
.h80{height: 80px;clear: both;display: block;}
.h100{height: 100px;clear: both;display: block;}
.h120{height: 120px;clear: both;display: block;}
.h150{height: 150px;clear: both;display: block;}
.pab{position: absolute;}
.pre{position: relative;}

/*=======通用样式=======*/
.wrap{min-width: 1300px;width:100%; overflow: hidden; position: relative;z-index: 0;}
.wrap .wrap_cont{width: 1200px; margin: auto; height: auto; position: relative;z-index: 2;}
.wrap .wrap_max{width:1920px; margin-left: -360px; position: relative; z-index: 1;}

.state-none{display: flex;padding-top: 20px;display: flex;flex-direction: column;align-items: center;}
.state-none img{width: 130px;height: 108px;margin-bottom: 21px;}
.state-none span{font-size: 14px;color: #D2DDEB;margin-bottom: 30px;}

.popup-bg{background: rgba(0,0,0,.5);width: 100%;height: 100%;position: fixed;left: 0;top: 0;z-index: 1000;}
</style>



