<script>
	export default {
		data() {
			return {
				// nav:['首页','行情','交易','公告中心'], //nav
				// curID:0,  //选择的nav
				interval: 3000, //banner图轮播
				// 二维码显示
				qrcode: -1,
				// id
				id: '',
				val: 0,
				//socket
				// socket:null,
				timer:null,
				WebSocketData:[], //首页socket数据
				newsList:[]
			}
		},
		filters: {
			tofixed4: function(value) {
				return value.toFixed(2);
			}
		},
		mounted() {
			/* let url = 'ws://app.r0297.cn' + '/websocket/' + this.$userID()
			this.socket = new WebSocket(url); */
			let socket = this.$socket()
			socket.onopen = ()=> {
				this.timer = setInterval(()=>{
					socket.send(this.$userID()+'_home')
				},1500)
			}
			socket.onmessage = (e)=> {
				// console.log(JSON.parse(e.data))
				this.WebSocketData = JSON.parse(e.data)
			}
			/* this.$options.sockets.onmessage = (data) =>{
				console.log(JSON.parse(data.data))
				this.$socket.send(this.$userID() + "_home")
			} */
			// this.$ajax('/account/getMyInfos',{
			// 	token:this.$userID()
			// })
			// .then(res=>{
			// 	if(res.data.status){
			// 		if(this.$route.name === 'Login'){
			// 			this.$router.push('/')
			// 		}
			// 	}else{
			// 		this.$router.push('/login')
			// 	}
			// });
			// banner
			// this.$ajax('/home/queryBanner',{
			// 	position: 2
			// })
			// .then(res=>{
			// 	if (res.data.status) {
			// 		this.banner = res.data.data
			// 	}
			// });
			// 公告
			this.$ajax('/home/queryNotice', {
				page: 1
			}).then(res=>{
				if (res.data.status) {
					this.newsList = res.data.data
				}
			})
		},
		destroyed() {
			console.log('clear')
			clearInterval(this.timer)
			this.timer = null
		},
		methods: {
			// 二维码显示
			qrcodeHover(e) {
				this.qrcode = e
			},
			// 二维码隐藏
			qrcodeLeave() {
				this.qrcode = -1
			},
		}
	}
</script>

