.downapp-btn-item a{width: 150px;height: 48px;display: flex;align-items: center;justify-content: center;background: #3387FF;border: 1px solid #3387FF;border-radius: 3px;box-sizing: border-box;margin-right: 24px;}
.downapp-btn-item a img{width: 20px;height: 20px;margin-right: 10px;}
.downapp-btn-item a span{font-size: 16px;color:white;line-height: normal;}
.downapp-btn-item a:hover{opacity: .8;}
.downapp-btn-item a.ios{background: #3387FF;border-color: #3387FF;margin-right:0;}
.downapp-qrcode{position: absolute;left: 0;top: 100%;display: flex;flex-direction: column;padding-top: 32px;}
.downapp-qrcode img{width: 134px;height: 134px;display: block;background: #fff;border: 1px solid #46586F;padding: 7px;margin-bottom: 6px;}
.downapp-qrcode span{text-align: center;color: #778AA2;font-size: 12px;}
/*  */
.ads-box{height:520px;background:rgba(30,35,74,1);}
.ads{height: 100%;display: flex;flex-direction: column;}
.ads-title{display: flex;flex-direction: column;justify-content: center;align-items: center;padding-top: 90px;padding-bottom: 80px;}
.ads-title h2{font-size: 32px;color: #E3E5FF;margin-bottom: 8px;}
.ads-title p{font-size: 16px;color: #778AA2;}
.ads-list{display: flex;}
.ads-item{display: flex;flex-direction: column;align-items: center;flex: 1;box-sizing: border-box;padding: 0 30px;}
.ads-item img{height: 64px;}
.ads-item h3{font-size: 24px;color: #E3E5FF;margin: 14px 0 10px 0;}
.ads-item p{font-size: 14px;color: #778AA2;text-align: center;}
/*  */
.quotation-box{background: #081724;}
.quotation{height: 158px;display: flex;align-items: center;}
.quotation-item{width: 221px;height: 110px;display: flex;flex-direction: column;justify-content: center;background: #141F3A;margin-right: 23px;box-sizing: border-box;padding:0 24px;}
.quotation-item:last-child{margin-right: 0;}
.quotation-head{display: flex;justify-content: space-between;font-size: 13px;}
.quotation-head span:first-child{color: #D1D5EB;}
.quotation-head span:nth-child(2){color: #02AD8F;}
.quotation-cennter{color: #02AD8F;font-size: 24px;margin: 6px 0;font-weight: bold;}
.quotation-bottom{color: #778AA2;}
.down{color: #D14B64 !important;}

.banner{height: 500px;}
.banner img{width: 100%;max-width: 100%;display: block;min-height: 500px;}
</style>



