<template>
	<div>
		<div class="order-box">
			<div class="order-content main">
				<div class="order-head">
					<div class="order-title"><img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQBAMAAADt3eJSAAAAIVBMVEWZmZlHcEyZmZmZmZmZmZmZmZmYmJiZmZmZmZmZmZmZmZkjQ1aoAAAAC3RSTlP/AJ3pyNssjXlAXHnrPpAAAABNSURBVAjXYxBzYAACI0EGEQYwaGQQgDAUsTFY09LSssAioaGhwWCRjo6OdhCDuby8vAgs5eLiDFbMMnPmJBzmwBhQSxcyiDkbA0G4IAADgw9B1ARZhAAAAABJRU5ErkJggg==" alt="">委托订单</div>
					<div class="order-tab">
						<span v-for="(item,index) in tabs" :key="index+item" :class="tabCur == index ? 'cur':''" @click="tabTap(index)">{{item}}</span>
					</div>
				</div>
				<div class="trade-bottom">
					<div class="trade-bottom-head">
						<p>币种</p>
						<p>数量</p>
						<p>委托价(USDT)</p>
						<p>杠杆倍数(倍)</p>
						<p>时间</p>
					</div>
					<template v-if="WebSocketData.length != 0">
					<div class="trade-bottom-item" v-for="(item,index) in WebSocketData" :key="index">
						<p>{{item.coin}}</p>
						<p>{{item.coinnum}}</p>
						<p>{{item.price}}</p>
						<p>{{item.gearing}}</p>
						<p v-if="tabCur == 0">{{item.createtimeStr}}</p>
						<p v-if="tabCur == 1">{{item.succestimeStr}}</p>
						<p v-if="tabCur == 2">{{item.canceltimeStr}}</p>
					</div>
					</template>
					<div class="state-none" v-else>
						<img src="../static/images/none.png" alt="">
						<span>暂无订单</span>
					</div>
					<div class="loading-more" v-if="tabCur == 1 && WebSocketData2.length >= 5">
						<img class="loading-animate" src="../static/images/loading.png" v-show="loadingType === 1">
						<span class="loading-text" @click="loadingMore">{{loadingType === 0 ? contentText.contentdown : (loadingType === 1 ? contentText.contentrefresh : contentText.contentnomore)}}</span>
					</div>
				</div>
			</div>
		</div>
		<!-- 修改止盈止损弹窗 -->
		<div class="popup-bg" v-if="coverStatus"></div>
		<div class="trade-popup" v-if="coverStatus">
			<div class="trade-popup-wrap">
				<div class="trade-popup-title">
					<span>修改·<span :class="WebSocketData[stopIndex].type == 1?'green':'red'" style="margin: 0 5px;">{{WebSocketData[stopIndex].type == 1?'开多':'开空'}}</span>{{WebSocketData[stopIndex].coinname}}</span>
					<img src="../static/images/close.png" alt="" title="关闭" @click="coverClose">
				</div>
				<div class="trade-popup-item">
					<span>止盈价格</span>
					<div class="trade-popup-item-right">
						<input class="tc" type="tel" v-model="stopwin" placeholder="请输入止盈价格" />
						<b>USDT</b>
					</div>
				</div>
				<div class="trade-popup-item">
					<span>止损价格</span>
					<div class="trade-popup-item-right">
						<input class="tc" type="tel" v-model="stopdonat" placeholder="请输入止损价格" />
						<b>USDT</b>	
					</div>
				</div>
				<div style="height: 10px;"></div>
				<div class="trade-popup-btn"  @click="editConfirm">确定</div>	
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				tabs:['委托中','委托成功','已取消'],
				tabCur:0,  //tab选中
				timer:null, //socket发送计时器
				WebSocketData:'',  //socket数据
				// list:[],  //历史记录列表数据
				WebSocketData2:'',  //历史记录列表
				deposit:0,// 保证金
				CoverIndex:0,  //平仓的索引
				coverStatus:false,
				// 止盈
				stopwin:'',
				// 止损
				stopdonat:'',
				// 止损index
				stopIndex:0,
				// ordercode
				ordercode:'',
				// 支付密码
				payWordStr:'',
				payWordStatus:false,  //支付确认弹窗
				// 买入卖出type
				orderType:0,
				//加载更多
				page: 1,
				loadingType: 0,
				contentText: {
					contentdown: "点击显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				}, 
			}
		},
		created() {
			
		},
		mounted() {
			this.loadingType = 0
			this.page = 1
			//this.initSocket()
			this.tabTap(0);
			this.$ajax('/account/getMyInfos',{
				token:this.$userID()
			})
			.then(res=>{
				if(res.data.status){
					if(this.$route.name === 'Login'){
						this.$router.push('/')
					}
				}else{
					this.$router.push('/login')
				}
			})
		},
		destroyed() {
			console.log('clear')
			clearInterval(this.timer)
			this.timer = null
		},
		methods:{
			// 初始化socket
			initSocket(){
				let socket = this.$socket()
				socket.onopen = ()=> {
					socket.send(this.$userID()+'_contractall')
					this.timer = setInterval(()=>{
						socket.send(this.$userID()+'_contractall')
					},1500)
				}
				socket.onmessage = (e)=> {
					// console.log(JSON.parse(e.data))
					this.WebSocketData = JSON.parse(e.data)
				}
				socket.onclose = ()=>{
					clearInterval(this.timer);
					this.timer = null
					console.log('close')
				}
			},
			// tab选择
			tabTap(e){
				this.tabCur = e
				this.loadingType = 0
				this.$ajax('/trade/queryMyEntrust',{
					token:this.$userID(),
					status:this.tabCur+1,
					page:1
				}).then(res=>{
					if(res.data.status){
						this.WebSocketData = res.data.data;
					}
				})
				
			},
			// 修改止盈止损（弹窗）
			editTap(e){
				this.stopIndex = e
				this.coverStatus = true
				// 
				this.ordercode = this.WebSocketData[e].ordercode
			},
			// 修改确认操作
			editConfirm(){
				let rex = /^[0-9]+(.[0-9]{1,4})?$/
				if(this.stopwin == '' || this.stopdonat == ''){
					this.$alert('请输入价格','',1200)
				}else if(!rex.test(this.stopwin) || !rex.test(this.stopdonat)){
					this.$alert('输入价格不正确','',1200)
				}else{
					this.$ajax('/trade/handleEditOrder',{
						token:this.$userID(),
						ordercode:this.ordercode,
						stopwin:this.stopwin,
						stopdonat:this.stopdonat
					}).then(res=>{
						if(res.data.status){
							this.$message({
								type: 'success',
								message: res.data.desc
							});
							this.coverStatus = false
						}else{
							this.$message({
								type: 'error',
								message: res.data.desc
							});
						}
					})
				}
			},
			// 止盈止损修改 弹窗关闭
			coverClose(){
				this.coverStatus = false
				this.stopwin = ''
				this.stopdonat = ''
			},
			// 平仓
			coverTap(e){
				this.CoverIndex = e
				this.$confirm('是否要平仓?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$ajax('/trade/handleCloseOrder',{
						token:this.$userID(),
						ordercode:this.WebSocketData[this.CoverIndex].ordercode
					}).then(res=>{
						if(res.data.status){
							this.$message({
								type: 'success',
								message: res.data.desc
							});
							// 查询USDT余额
							this.$ajax('/account/getWallet',{
								token:this.$userID(),
								coin:'USDT'
							}).then(res=>{
								if(res.data.status){
									this.usdt_num = res.data.data
								}
							})
						}else{
							this.$message({
								type: 'error',
								message: res.data.desc
							});
						}
					})
				})
			},
			// 加载更多
			loadingMore(){
				if (this.loadingType !== 0) {
					return;
				}
				this.loadingType = 1;
				
				this.$ajax('/trade/queryContractOrder',{
					token:this.$userID(),
					page:++this.page,
					status:2
				}).then(res=>{
					if(res.data.status){
						this.WebSocketData2 = this.WebSocketData2.concat(res.data.data)
					}
					setTimeout(() => {
						if(res.data.data.length == 0){
							this.loadingType = 2;
							return;
						}
						this.loadingType = 0;
					}, 300);
				})
			}
		}
	}
</script>

<style>
/*  */
.trade-popup{width: 472px;background: #fff;position: fixed;left: 50%;top: 50%;transform: translate(-50%,-50%);z-index: 1001;border-radius: 6px;}
.trade-popup-wrap{padding: 40px 48px;}
.trade-popup-title{text-align: center;margin-bottom: 24px;}
.trade-popup-title span{font-size: 24px;font-weight: bold;}
.trade-popup-title img{position: absolute;right: 0;top: 0;padding: 15px;width: 28px;cursor: pointer;}
.trade-popup-item{height: 48px;border: 1px solid #D9D9D9;border-radius: 4px;display: flex;align-items: center;justify-content: space-between;padding: 0 16px;font-size: 16px;margin-bottom: 16px;}
.trade-popup-item span{}
.trade-popup-item input{height: 100%;line-height: 48px;border: 0;outline: none;text-align: right;}
.trade-popup-item input.tc{text-align: left;}
.trade-popup-item-right{display: flex;align-items: center;height: 100%;}
.trade-popup-item-right p{}
.trade-popup-item-right b{margin-left: 10px;}
.trade-popup-btn{height: 48px;background:rgba(255,170,0,1);border-radius:4px;text-align: center;line-height: 48px;font-size: 16px;cursor: pointer;}
.trade-popup-btn:active{opacity: 1;}
.trade-popup-btn:hover{opacity: .85;}
/*  */
.loading-more{display: flex;align-items: center;justify-content: center;height: 50px;}
.loading-text{color: #D1D5EB;cursor: pointer;}
.loading-animate{animation:1.2s linear loading infinite;width: 20px;height: 20px;margin-right: 4px;}
@keyframes loading{
	from{}
	to{transform: rotate(360deg);}
}
/*  */
/* .trade-bottom{background: #081724;} */
.trade-bottom-tab{display: flex;width: 400px;height: 45px;line-height: 45px;}
.trade-bottom-tab span{flex: 1;text-align: center;font-size: 16px;color: #778AA2;cursor: pointer;}
.trade-bottom-tab span.cur{color: #FFAA00;position: relative;}
.trade-bottom-tab span.cur:after{content: '';background: #FFAA00;position: absolute;left: 0;bottom: 0;height: 2px;width: 100%;}
.trade-bottom-head{display: flex;height: 64px;align-items: center;background: #F2F2F2;}
.trade-bottom-head p{flex: 1;text-align: center;color: #999999;}
.trade-bottom-head p span{display: block;}
.trade-bottom-item{display: flex;height: 74px;align-items: center;border-bottom: 1px solid #EDEDED;}
.trade-bottom-item p{flex: 1;text-align: center;color: #333333;font-size: 12px;}
.trade-bottom-item p span{display: block;}
.trade-btn{flex-direction: column;display: flex;align-items: center;}
.trade-bottom-item p a{width: 70px;height: 28px;text-align: center;line-height: 28px;background: #3387FF;font-size: 12px;color: white;transition: .25s all;border-radius: 5px}
.trade-bottom-item p a:nth-child(2){margin-top: 6px;}
.trade-bottom-item p a:hover{color: #fff;opacity: .8;}
.red{color: #D14B64 !important;}
.green{color: #02AD8F !important;}
/*  */
.order-box{padding: 16px 0;background: #F8F9FB;}
.order-content{background: #FFFFFF;min-height: 700px;}
.order-head{}
.order-title{font-size: 16px;color: #666666;display: flex;align-items: center;padding: 16px 0 0 30px;margin-bottom: 10px;}
.order-title img{margin-right: 6px;}
.order-tab{display: flex;height: 60px;}
.order-tab span{width: 200px;text-align: center;line-height: 60px;font-size: 16px;color: #666666;cursor: pointer;}
.order-tab span:active{background: ;}
.order-tab span.cur{color: #3387FF;position: relative;}
.order-tab span.cur:after{content: '';width: 100%;height: 2px;background: #3387FF;position: absolute;left: 0;bottom: 0;}

</style>



