              G币coin外部威胁情报处理标准
              适用范围
              本流程适用于G币安全响应中心（GBcoin@126.com）所收到的所有情报。
              实施日期
              本文档自发布之日起实行。
              基本原则
              1. G币coin非常重视自身产品和业务的安全问题，我们承诺，对每一位报告者反馈的问题都有专人进行跟进、分析和处理，并及时给予答复。
              2. G币coin支持负责任的漏洞披露和处理过程，我们承诺，对于每位恪守白帽子精神，保护用户利益，帮助G币coin提升安全质量的用户，我们将给予感谢和回馈。
              3. G币coin反对和谴责一切以漏洞测试为借口，利用安全漏洞进行破坏、损害用户利益的黑客行为，包括但不限于利用漏洞盗取用户隐私及虚拟财产、入侵业务系统、窃取用户数据、恶意传播漏洞等。
              4. G币coin反对和谴责一切利用安全漏洞恐吓用户、攻击竞争对手的行为。
              5. G币coin认为每个安全漏洞的处理和整个安全行业的进步，都离不开各方的共同合作。希望企业、安全公司、安全组织、安全研究者一起加入到“负责任的漏洞披露”过程中来， 一起为建设安全健康的互联网而努力。
              威胁情报反馈与处理流程
              [ 报告阶段 ]
              威胁情报报告者通过G币coin威胁情报接收邮箱（GBcoin@126.com）反馈威胁情报。
              [ 处理阶段 ]
              1. 一个工作日内，G币coin安全应急响应中工作人员会确认收到的威胁情报报告并跟进开始评估问题（状态：审核中）。
              2. 三个工作日内，工作人员处理问题、给出结论并计分（状态：已确认/已忽略）。必要时会与报告者沟通确认，请报告者予以协助。
              [ 修复阶段 ]
              1. 技术部门修复威胁情报中反馈的安全问题并安排更新上线（状态：已修复）。修复时间根据问题的严重程度及修复难度而定，一般来说，严重和高风险问题 24小时内，中风险三个工作日内，低风险七个工作日内。客户端安全问题受版本发布限制，修复时间根据实际情况确定。
              2. 威胁情报报告者复查安全问题是否修复（状态：已复查/复查异议）。
              [ 完成阶段 ]
              1. 发布上月威胁情报处理公告，向上月的威胁情报报告者致谢并公布威胁情报处理情况；严重或重大影响威胁情报会单独发布紧急安全公告。
              2. 威胁情报报告者可以使用积分兑换安全币，通过安全币置换现金或现价对等的平台货币，置换完成后，为威胁情报报告者发出奖励；同时不定期也会有奖励及线下活动。
              争议解决办法
              在威胁情报处理过程中，如果报告者对处理流程、威胁情报评定、威胁情报评分等具有异议的，请通过当前威胁情报邮箱及时沟通。G币coin安全应急响应中心将根据威胁情报报告者利益优先的原则进行处理，必要时可引入外部人士共同裁定。
