import Vue from 'vue' //引入Vue
import Router from 'vue-router' //引入vue-router

import Header from '~/pages/header'
import Footer from '~/pages/footer'
import Index from '~/pages/index' //首页
import Login from '~/pages/login' //登陆
import Register from '~/pages/register' //注册
import Forgot from '~/pages/forgot' //注册
import Quotation from '~/pages/quotation' //行情
import Trade from '~/pages/trade' //交易
import Notice from '~/pages/notice' //公告
import NoticeDetail from '~/pages/notice_detail' //公告详情
import Order from '~/pages/order' //订单
import OrderE from '~/pages/order2' //订单
import Wallet from '~/pages/wallet' //钱包
import Agr from '~/pages/agr' //协议


Vue.use(Router)

export default new Router({
	routes: [ //配置路由，这里是个数组
		{ //每一个链接都是一个对象
			path: '/', //链接路径
			name: 'Index', //路由名称，
			components: { //对应的组件模板
				default: Index,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/login',
			name: 'Login',
			components: {
				default: Login,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/register',
			name: 'Register',
			components: {
				default: Register,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/forget',
			name: 'Forgot',
			components: {
				default: Forgot,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/quotation',
			name: 'Quotation',
			components: {
				default: Quotation,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/trade',
			name: 'Trade',
			components: {
				default: Trade,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/notice',
			name: 'Notice',
			components: {
				default: Notice,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/notice/detail/:id',
			name:'NoticeDetail',
			components: {
				default: NoticeDetail,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/order',
			name:'Order',
			components: {
				default: Order,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/order_entrust',
			name: 'OrderEntrust',
			components: {
				default: OrderE,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/wallet',
			name:'Wallet',
			components: {
				default: Wallet,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/agr',
			name: 'Agr',
			components: {
				default: Agr,
				header: Header,
				footer: Footer
			}
		}
	]
})

const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
};



