<template>
	<div>
		<div class="detail-box">
			<div class="detail-content main">
				<div class="detail-box-wrap">
					<div class="detail-box-title">
						<h3>{{details.title}}</h3>
						<p>日期：{{details.timestr}}</p>
					</div>
					<div class="details" v-html="details.content"></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				details:{},
			}
		},
		created() {
			// console.log(this.$route.params.id)
			let id = this.$route.params.id
			this.$ajax('/home/getNotice',{
				detailid:id
			}).then(res=>{
				if(res.data.status){
					this.details = res.data.data
					// this.details.content = res.data.data.content.replace(/\<img/g, '<img style="max-width:100%;"')
				}
			})
		},
	}
</script>

<style>
/*  */
.detail-box-wrap{padding:30px 50px;}
.detail-box{padding: 16px 0;background: #F8F9FB;}
.detail-content{background: white;}
.detail-box-title{text-align: center;color: #333333;border-bottom: 1px solid #E2E2E2;padding-bottom: 20px;}
.detail-box-title h3{font-size: 22px;margin-bottom: 10px;}
.detail-box-title p{font-size: 12px;}
.details{padding-top: 20px;color: #666666;min-height: 550px;}
</style>



