<script>
	export default {
		data() {
			return {
				coin:'', //币种
				//socket
				timer:null,
				timer2:null,
				WebSocketData:[],
				curID:0,  //选中的币种
				normalCoin:'BTC/USDT',  //选中的币种
				tableData:{ //图标动态数据
					depth:{
						asks:[],
						bids:[]
					},
					dto:{}
				}, 
			}
		},
		filters: {
			tofixed2: function(value) {
				return value.toFixed(2);
			},
			tofixed4: function(value) {
				return value.toFixed(6);
			}
		},
		created() {
			
		},
		mounted() {
			this.initSocket()  //初始化
		},
		destroyed() {
			console.log('clear')
			clearInterval(this.timer)
			this.timer = null
			clearInterval(this.timer2)
			this.timer2 = null
		},
		methods:{
			// 初始化socket
			initSocket(){
				let socket = this.$socket()
				socket.onopen = ()=> {
					this.timer = setInterval(()=>{
						socket.send(this.$userID()+'_home')
					},1500)
					socket.send(this.$userID()+'_kline_'+this.normalCoin)
					this.timer2 = setInterval(()=>{
						socket.send(this.$userID()+'_kline_'+this.normalCoin)
					},1500)
				}
				socket.onmessage = (e)=> {
					// console.log(JSON.parse(e.data))
					if(JSON.parse(e.data) instanceof Array){
						this.WebSocketData = JSON.parse(e.data)
					}else{
						this.tableData = JSON.parse(e.data)
					}
					// this.tableData = this.WebSocketData[this.curID]
				}
			},
			// 切换图表数据
			coinChange(e){
				let coin = this.WebSocketData[e].coin
				console.log(coin)
				this.tradingViewShow(coin)
				this.normalCoin = this.WebSocketData[e].name
			},
			// 图表方法
			tradingViewShow(coin){
				new TradingView.widget({
					"autosize": true,
					"symbol": "HUOBI:"+coin+'USDT',
					"interval": "1",
					"timezone": 'Asia/Shanghai',
					"theme": "Dark",
					"style": "1",
					"locale": "zh_CN",
					"toolbar_bg": "#f1f3f6",
					"enable_publishing": false,
					"hide_legend": true,
					"save_image": false,
					"studies": ["MACD@tv-basicstudies", "BB@tv-basicstudies",
						"Volume@tv-basicstudies", "VWAP@tv-basicstudies"
					],
					"container_id":'tradingview_2ac30'
				});
			}
		}
	}
</script>

<style scoped>
