.data-nav-item{position: relative;height: 55px;font-size: 16rpx;color: #3387FF;font-weight: bold;line-height: 55px;padding: 0 16px;cursor: pointer;}
.data-nav-item::after{content: '';position: absolute;bottom: 0;left: 0;right: 0;width: 100%;height: 2px;background: #3387FF;}
