.data-table .table-head{display: flex;}
.data-table .table-head .td{flex: 1;font-size: 12px;color: #666666;padding:16px;text-align: center;}
.data-table .table-body .tr{display: flex;padding: 16px 0;border-bottom: 1px solid #EDEDED;}
.data-table .table-body .tr:hover{background: #EEF5FF;cursor: pointer;}
.data-table .table-body .td{flex: 1;font-size: 14px;color: #333333;padding: 0 16px;text-align: center;}
.data-table .table-body .td-desc{font-size: 14px;color: #999999}
/*  */
.quotation-page{background: #F8F9FB;}
.quotation-content{padding: 16px 0;display: flex;justify-content: space-between;}
.quotation-left{width: 520px;background: #081724;min-height: 909px;}
.quotation-left-head{display: flex;height: 45px;line-height: 45px;}
.quotation-left-head span{flex: 1;text-align: center;color: #778AA2;font-size: 12px;}
