.quotation-left-list-item{display: flex;height: 72px;align-items: center;}
.quotation-left-list-item:hover{background: #141f30;}
.items{flex: 1;text-align: center;}
.left-item-name{font-size: 18px;color: #D1D5EB;}
.left-item-name span{font-size: 12px;color: #778AA2;}
.left-item-price{font-size: 18px;color: #D1D5EB;}
.left-item-price span{display: block;color: #778AA2;font-size: 12px;}
.left-item-percent{display: flex;justify-content: center;}
.left-item-percent span{width: 84px;height: 34px;text-align: center;line-height: 34px;border-radius: 3px;color: #fff;background: #02AD8F;}
.left-item-percent span.minus{background: #D14B64;}
.left-item-all{font-size: 18px;color: #D1D5EB;}

.quotation-right{width: 660px;color: #D1D5EB;background: #081724;}
.quotation-right-title{height: 45px;text-align: center;line-height: 45px;font-size: 16px;color: #fff;}
.quotation-right-top{padding: 0 15px;min-height: 105px;display: flex;justify-content: space-between;align-items: center;}
.quotation-right-top-left{}
.quotation-right-top-left p{color: #02AD8F;}
.quotation-right-top-left p span{color: #778AA2;margin-right: 10px;}
.quotation-right-top-left p:first-child{font-size: 36px;font-weight: bold;margin-bottom: 6px;}

.quotation-right-top-right{display: flex;align-content: flex-end;flex-direction: column;}
.quotation-right-top-right p{color: #46586F;display: flex;justify-content: space-between;line-height: 1.8;}
.quotation-right-top-right p span{color: #D1D5EB;margin-left: 30px;}


.quotation-right-bottom{padding: 20px 10px 0 10px;background: #141F30;}
.chart-deep-head{display: flex;margin-bottom: 10px;}
.chart-deep-head span{flex: 1;font-size: 12px;color: #778AA2;}
.chart-deep-head .flex1{flex: 0 0 10%;}
.chart-deep-head .flex2{flex: 0 0 20%;}
.chart-deep-box{display: flex;}
.chart-deep-list{flex: 1;}
.chart-deep-item{display: flex;height: 30px;line-height: 30px;}
.chart-deep-item span{flex: 1;font-size: 12px;}
.chart-deep-item span:first-of-type{flex: 0 0 20%;}
.chart-deep-item span:nth-of-type(2){flex: 0 0 30%;padding-right: 5px;}
.chart-deep-item span:nth-of-type(3){text-align: right;padding: 0 5px;color: #00AD76;background:rgba(2,173,143,.1);}
.chart-deep-item2{display: flex;height: 30px;line-height: 30px;}
.chart-deep-item2 span{flex: 1;font-size: 12px;}
.chart-deep-item2 span:first-of-type{padding: 0 5px;color: #D14B64;background:rgba(209,75,100,.1);}
.chart-deep-item2 span:nth-of-type(2){text-align: right;flex: 0 0 30%;padding-right: 5px;}
.chart-deep-item2 span:nth-of-type(3){text-align: right;flex: 0 0 20%;}
</style>



