<template>
	<div>
		<!-- <router-view name="header"></router-view>
		<router-view name="footer"></router-view> -->
		<div class="login-box">
			<div class="login main">
				<div class="login-content">
					<h2>登录账号</h2>
					<div class="login-item">
						<input type="text" placeholder="请输入手机号码" v-model="username">
					</div>
					<div class="login-item">
						<input type="password" placeholder="请输入登录密码" v-model="password">
					</div>
					<div class="login-link">
						<router-link :to="{name:'Forgot'}">忘记密码</router-link>
					</div>
					<div class="login-btn" @click="login()">登录</div>
				</div>
			</div>
		</div>
		<!-- <div style="height: 80px;background: #081724;"></div> -->
	</div>
</template>

<script>
	export default {
		data(){
			return{
				username:'',  //用户名,
				password:'' ,  //密码
				loading:{
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0)'
				}
			}
		},
		created() {
			
		},
		methods:{
			login(){
				if(!this.username){
					this.$alert('请输入手机号','',1200)
				}else if(!this.password){
					this.$alert('请输入登陆密码','',1200)
				}else{
					let loading = this.$loading(this.loading)
					this.$ajax('/customer/login',{
						login:this.username,
						password:this.password
					})
					.then(res=>{
						loading.close()
						// this.$alert(res.data.desc,'',1200)
						this.$message.success(res.data.desc);
						if(res.data.status){
							let token = (res.data.data).toString()
							//console.log(token)
							localStorage.setItem('userId',token)
							setTimeout(()=>{
								this.$router.push('/')
								//window.location.reload()
							},1000)
						}
					})
				}
			},
		}
	}
</script>

<style scoped>
/*  */
.login-box{background:#141F30 url(../static/images/login_bg.png) no-repeat;background-size: 100%;height: 772px;}
.login{position: relative;}
.login-content{width: 500px;position: absolute;right: 0;top: 117px;background: #fff;border-radius: 5px;box-sizing: border-box;padding: 40px 60px 48px 60px;}
.login-content h2{font-size: 28px;color: #141F30;text-align: center;font-weight: bold;margin-bottom: 20px;}
.login-item{width: 100%;border: 1px solid #D9D9D9;height: 48px;box-sizing: border-box;padding-left: 36px;background: url(../static/images/login_icon2.png) no-repeat 12px;border-radius:4px;margin-bottom: 16px;}
.login-item:nth-child(2){background-image: url(../static/images/login_icon1.png);}
.login-item input{border: 0;outline: none;height: 100%;line-height: 46px;width: 100%;font-size: 16px;}
.login-link{display: flex;justify-content: flex-end;margin-bottom: 24px;}
.login-link a{color: #999999;}
.login-link a:hover{opacity: .85;text-decoration: underline;}
.login-btn{height:48px;background:#3387FF;border-radius:4px;font-size: 16px;text-align: center;line-height: 48px;cursor: pointer;display: block;color: white}
.login-btn:hover{opacity: .85;}
.login-btn:active{opacity: 1;}
</style>



