							</div>
							<div class="bottom-table">
								<template v-if="list.length > 0">
									<div class="bottom-table-item" v-for="(item,index) in list" :key="index">
										<span>{{item.timestr}}</span>
										<!-- <span>JFJ3230959555</span> -->
										<span class="green2">{{item.remark}}</span>
										<span :class="item.isout == 0?'red2':'green2'">{{item.isout==0?'-':'+'}}{{item.cost}}</span>
