							<p>3、最小充值金额：1 USDT，小于最小金额的充值将不会上账且无法退回。</p>
							<p>4、您的充值地址不会经常改变，可以重复充值；如有更改，我们会尽量通过网站公告或邮件通知您。</p>
							<p>5、请务必确认电脑及浏览器安全，防止信息被篡改或泄露。</p>
						</div>
					</div>
					<!-- 提币 -->
					<div class="popup-bg" v-if="drawStatus"></div>
