						 <el-tabs v-model="tsTabsName">
							<el-tab-pane label="合约转逐仓" name="1"></el-tab-pane>
							<el-tab-pane label="逐仓转合约" name="2"></el-tab-pane>
						</el-tabs>
						<div class="draw-popup-box">
							<div class="draw-popup-item">
