<template>
	<div>
		<div class="top-box">
			<div class="top-box-wrap main">
				<router-link class="logo" tag="div" to='/'>
					<img src="../static/images/logo.png" alt="">
					<span>G币</span>
				</router-link>
				<div class="top-nav">
					<router-link exact-active-class="cur" :to='item.link' v-for="(item,index) in nav" :key='index'>{{item.name}}</router-link>
					<!-- <a href="javascript:;" :class="index == curID?'cur':''" v-for="(item,index) in nav" :key="item" @click="tabClick(index)">{{item}}</a> -->
				</div>
				<div class="top-btn" v-if="!isLogin">
					<a href="javascript:;" @click="tabClick('登陆')">登陆</a>
					<!-- <router-link :to="{name:'Login'}">登陆</router-link> -->
					<a href="javascript:;" class="full" @click="tabClick('注册')">注册</a>
				</div>
				<div class="top-btn2" v-else>
					<div class="top-btn2-order" >
						<img src="../static/images/order_icon.png" alt="">
						<span>订单</span>
						<img src="../static/images/more_order.png" alt="">
						<div class="top-btn2-down">
							<div class="item" @click="tabClick('order')">持仓订单</div>
							<div class="item" @click="tabClick('orderE')">委托订单</div>
						</div>
					</div>
					<div class="top-btn2-order" @click="tabClick('wallet')">
						<img src="../static/images/order_icon.png" alt="">
						<span>资产</span>
					</div>
					<div class="top-btn2-user">
						<img src="../static/images/order_user.png" alt="">
						<a href="javascript:;">{{username}}</a>
						<div class="top-btn2-down">
							<div class="item" @click="tabClick('wallet')">个人中心</div>
							<div class="item" @click="tabClick('logout')">退出登录</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				nav:[
					{
						name:'首页',
						link:'/'
					},
					{
						name:'行情',
						link:'/quotation'
					},
					{
						name:'合约',
						link:'/trade'
					},
					{
						name:'公告中心',
						link:'/notice'
					}
				],
				curID:NaN,  //选择的nav
				username:'', //用户名
				isLogin:false
			}
		},
		watch:{
			async $route(){
				if(this.$route.name === 'Index' && localStorage.getItem('userId')){
					await this.getUser();
				}
				if(this.$route.name === 'Trade'){
					if(!this.isLogin && !localStorage.getItem('userId')){
						this.$router.push('/login');
					}
				}
			}
		},
		created() {
			// console.log(this.$route.name)
			// // 判断当前路由是否是首页，如果是就添加首页选项选中
			// if(this.$route.name === 'Index'){
			// 	this.curID = 0
			// }else if(this.$route.name === 'Quotation'){
			// 	this.curID = 1
			// }else if(this.$route.name === 'Trade'){
			// 	this.curID = 2
			// }else if(this.$route.name === 'Notice' || this.$route.name === 'NoticeDetail'){
			// 	this.curID = 3
			// }else{
			// 	this.curID = NaN
			// }
			this.getUser();
		},
		mounted() {
			
		},
		destroyed(){
			
		},
		methods:{
			// 获取用户信息
			getUser(){
				return new Promise((r,rej)=>{
					this.$ajax('/account/getMyInfos',{
						token:this.$userID()
					}).then(res=>{
						if(res.data.status){
							this.username = res.data.data.phone
							this.isLogin = true;
							if(this.$route.name === 'Login'){
								this.$router.push('/')
							}
						}else{
							//this.$router.push('/login')
							this.curID = NaN
							this.isLogin = false;
						}
						r();
					})
				})
			},
			// 点击导航
			tabClick(e){
				if(!this.isLogin && e != '登陆' && e != '注册'){
					this.$router.push('/Login')
					return
				}
				if(e >= 0){
					this.curID = e
					if(e == 0){
						this.$router.push('/')
					}else if(e == 1){
						this.$router.push('/quotation')
					}else if(e == 2){
						this.$router.push('/trade')
					}else if(e == 3){
						this.$router.push('/notice')
					}
				}else{
					this.curID = '-1'
					if(e == '登陆'){
						this.$router.push('/Login')
					}else if(e == '注册'){
						this.$router.push('/Register')
					}else if(e == 'logout'){
						this.isLogin = false;
						this.$router.push('/Login');
						localStorage.removeItem('userId');
						//window.location.reload()
					}else if(e == 'order'){
						this.$router.push('/order')
					}else if(e == 'orderE'){
						this.$router.push('/order_entrust')
					}else if(e == 'wallet'){
						this.$router.push('/wallet')
					}
				}
			}
		},
	}
</script>

<style>
/*  */
.top-box{background: white;}
.top-box-wrap{display: flex;align-items: center;height: 60px;position: relative;}
.logo{margin-right: 66px;display: flex;align-items: center;font-size: 18px;color: #333333;font-weight: bold;cursor: pointer;}
.logo img{width: 32px;height: 32px;;display: block;margin-right: 10px;}
.top-nav{display: flex;align-items: center;line-height: 60px;}
.top-nav a{color: #333333;transition: .25s all;padding: 0 20px;margin-right: 40px;}
.top-nav a:last-child{margin-right: 0;}
.top-nav a.cur{color: #3387FF;position: relative;}
.top-nav a:hover{color: #3387FF;}
.top-nav a.cur:after{content: '';background: #3387FF;width: 36px;height: 2px;position: absolute;left: 50%;bottom: 10px;margin-left: -18px;}
.top-btn{position: absolute;right: 0;top: 50%;margin-top: -15px;display: flex;}
.top-btn a{color: #3387FF;width: 78px;height: 28px;border: 1px solid #3387FF;text-align: center;line-height: 28px;margin-left: 24px;border-radius: 3px;}
.top-btn a.full{background: #3387FF;color: white;}
.top-btn a:hover{opacity: .85;}

.top-btn2{position: absolute;right: 0;top: 50%;margin-top: -15px;display: flex;align-items: center;}
.top-btn2 a{color: #D1D5EB;display: flex;align-items: center;line-height: normal;}
.top-btn2-order{position: relative;margin-right: 24px;display: flex;align-items: center;cursor: pointer;height: 30px;}
.top-btn2-order span{font-size: 14px;color: #333333;padding: 0 10px}
.top-btn2-user{background: url(../static/images/more_order.png) no-repeat right;position: relative;display: flex;align-items: center;padding: 6px 18px 6px 0;}
.top-btn2-user img{margin-right: 6px;}
.top-btn2-user a{font-size: 16px; color: #333333}
/* .top-btn2-user a:hover{color: #aaa;} */
.top-btn2-down{display: flex;flex-direction: column;width: 100px;;background:white;position: absolute;right: 0;top: 30px;box-shadow:0px 2px 12px 0px rgba(0,0,0,0.1);border-radius: 4px;display: none;z-index: 999;overflow: hidden;}
.top-btn2-down .item{width: 100%;height: 40px;font-size: 14px;color: #666666;text-align: center;line-height: 40px;border-bottom: 1px solid #EDEDED;}
.top-btn2-down .item:hover{color: #3387FF;cursor: pointer;}
.top-btn2-user:hover .top-btn2-down{display: block;}
.top-btn2-order:hover .top-btn2-down{display: block;}
</style>



