Vue.prototype.$socket = function(){
	return new WebSocket('ws://localhost:8008' + '/websocket/' + this.$userID())
}

Vue.prototype.$userID = function(){
	return localStorage.getItem('userId') || '123';
}

Vue.prototype.$token = function(res){
	if(res.data.desc != '1001'){
		this.$message.error(res.data.desc);
	}else{
		this.$message.error(res.data.desc);
		localStorage.removeItem('userId')
		window.location.reload()
	}
}



new Vue({
	router,
	axios,
	el: '#app',
	render: h => h(App)
})



