<template>
	<div>
		<div class="footer">
			<div class="footer-content main">
				 <div class="footer-top">
					 <div class="top-left">
						 <dl class="footer-list">
							 <dt @click="goto">平台</dt>
							 <dd @click="goto">关于我们</dd>
							 <dd @click="goto">隐私政策</dd>
							 <dd @click="goto">用户协议</dd>
							 <!-- <dd>法律声明</dd> -->
							 <dd @click="goto">平台安全</dd>
						 </dl>
						 <dl class="footer-list">
							 <dt @click="goto">服务</dt>
							 <!-- <dd>帮助中心</dd> -->
							 <dd @click="goto">APP下载</dd>
							 <dd @click="goto">费率标准</dd>
						 </dl>
						 <dl class="footer-list">
							 <dt>联系我们</dt>
							 <dd>邮箱地址：{{info.service_phone}}</dd>
							 <!-- <dd>联系电话：1532131234</dd> -->
						 </dl>
					 </div>
					 <img class="logo" src="../static/images/logo2.png" alt="">
				 </div>
				 <div class="footer-bottom">
					 © 2019-2020 版权所有 ICP备案证书号：琼ICP备13459900495 POWERED byCcopyright（C）GCOIN2019-2020，All Rights Reserved
				 </div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				info: ''
			}
		},
		created() {
			this.$ajax('utils/getSysInfo', {}).then(res => {
				if (res.data.status) {
					this.info = res.data.data
				}
			})
		},
		methods:{
			goto(){
				this.$router.push('/agr');
				document.body.scrollTop = 0;
			}
		}
	}
</script>

<style>
	/*  */
	.footer {
		background: #152030;
	}

	.footer-content {
		
	}
	.footer-top{display: flex;justify-content:space-between;padding:18px 0;border-bottom: 1px solid #A0A0A0;}
	.footer-content .top-left{display: flex;}
	.footer-content .logo{width: 180px;height: 60px;margin-top: 15px;}

	.footer-list{
		margin-right: 80px;
	}
	.footer-list dt{font-size: 16px;color: white;margin-bottom: 10px;}
	.footer-list dd{font-size: 14px;color: #A1A7B3;margin-bottom: 10px;cursor: pointer;}
	.footer-list dd:hover{text-decoration: underline}
	.footer-list dd:last-of-type{margin-bottom: 0;}

	.footer-bottom{padding: 15px 0;font-size: 12px;color: #858585;text-align: center;}
</style>



