<template>
	<div>
		<div class="notice-box">	
			<div class="notice-list main">
				<div class="notice-list-wrap" v-if="list.length > 0">
					<router-link :to="{ name: 'NoticeDetail', params: { id: item.id }}" class="notice-item" v-for="(item,index) in list" :key="index">
						<p>{{item.title}}</p>
						<span>{{item.timestr}}</span>
					</router-link>	
				</div>
				<div class="state-none" v-else>
					<img src="../static/images/none.png" alt="">
					<span>暂无数据</span>
				</div>
				<div class="loading-more" v-if="list.length >= 5">
					<img class="loading-animate" src="../static/images/loading.png" v-show="loadingType === 1">
					<span class="loading-text" @click="loadingMore">{{loadingType === 0 ? contentText.contentdown : (loadingType === 1 ? contentText.contentrefresh : contentText.contentnomore)}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				list:[],
				//加载更多
				page: 1,
				loadingType: 0,
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				}, 
			}
		},
		created() {
			
		},
		mounted() {
			this.$ajax('/home/queryNotice',{
				page:1
			}).then(res=>{
				if(res.data.status){
					this.list = res.data.data
				}
			})
		},
		methods:{
			
		}
	}
</script>

<style scoped>
/*  */
.notice-box{background: #F8F9FB;min-height: 650px;padding: 16px 0;}
.notice-list{background: white;}
.notice-item{padding:16px;color: #333333;display: flex;flex-direction: column;border-bottom: 1px solid #E2E2E2;background: white;}
.notice-item p{font-size:16px;margin-bottom: 6px;}
.notice-item span{font-size: 12px;}
/* .notice-item:hover{background: #141f30;} */
.notice-item p:hover{color: #3387FF;text-decoration: underline;}
.notice-page{height: 80px;display: flex;align-items: center;justify-content: center;}
/* .el-dialog, .el-pager li{background: none;color: #778AA2;}
.el-pager li:hover{color: #FFAA00;}
.el-pager li.active{color: #FFAA00;}
.el-pagination .btn-prev ,.el-pagination .btn-next{background: none;color: #778AA2;}
.el-pagination button:disabled{background: none;} */
.el-pagination.is-background .el-pager li:not(.disabled).active{background: #FFAA00;}
.el-pagination.is-background .el-pager li:hover{color: #FFAA00;}
</style>



